import Layout from '../components/Layout';
import { Card } from 'react-bootstrap';

const Project = () => (
    <Layout>
        <h2>Projects</h2>
        <div>
        <Card style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title>Itune search app</Card.Title>
    <Card.Text>
      Itunes search app was created using React, HTML, Javascript and CSS. The data used for this app was obtained from the itunes search API.
      The app was then deployed on heroku.
    </Card.Text>
    <Card.Link href="https://itunes-search-app-fn.herokuapp.com/" target="_blank">Link to app</Card.Link>
  </Card.Body>
</Card>
        </div>
        <div>
        <Card style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title>Minesweeper</Card.Title>
    <Card.Text>
      This minesweeper game was developed using React and javascript as a project for my portfolio.
    </Card.Text>
    <Card.Link href="https://protected-scrubland-73603.herokuapp.com/">Link to game</Card.Link>
  </Card.Body>
</Card>
        </div>
        <div>
        <Card style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title>Hypersports Online Store</Card.Title>
    <Card.Text>
      This hypothetical online store was created to enhance my coding skills and was created using javascript, HTML and CSS.
    </Card.Text>
    <Card.Link href="https://fayyaadh4.github.io/hyperSports/">Link to GitHub</Card.Link>
  </Card.Body>
</Card>
        </div>
        <div>
        <Card style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title>Card Title</Card.Title>
    <Card.Subtitle className="mb-2 text-muted">Card Subtitle</Card.Subtitle>
    <Card.Text>
      Some quick example text to build on the card title and make up the bulk of
      the card's content.
    </Card.Text>
    <Card.Link href="#">Card Link</Card.Link>
    <Card.Link href="#">Another Link</Card.Link>
  </Card.Body>
</Card>
        </div>
    </Layout>
)

export default Project;